export default async function PrivacyPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = await params;
  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">Privacy groups — {id}</h3>
      <p>Создание/обновление privacy groups и тестовая отправка private tx.</p>
    </div>
  );
}

